import TokenService from "./TokenService";
import authenticatedApi from "./authenticatedApi";
import publicApi from "./publicApi";

class AuthService {
  async login(email, password) {
    return publicApi
      .post("/user/login", {
        email,
        password,
      })
      .then((response) => {
        if (response.data) {
          TokenService.setSession(response.data);
        }
        return response.data;
      });
  }

  async signup({ email, password, domain, firstName, lastName }) {
    return publicApi
      .post("/user/signup", {
        email,
        password,
        domain,
        firstName,
        lastName,
      })
      .then((response) => {
        return response.data;
      });
  }

  async confirmSignup(token) {
    return publicApi
      .post("/user/confirm_admin", {
        token,
      })
      .then((response) => {
        if (response.data) {
          TokenService.setSession(response.data);
        }
        return response.data;
      });
  }

  async forgotPassword(email) {
    return publicApi
      .post("/user/forgot_password", {
        email,
      })
      .then((response) => {
        return response.data;
      });
  }

  async resetPassword(refreshToken, newPassword) {
    return publicApi
      .post("/user/reset_password", { refreshToken, newPassword })
      .then((response) => {
        if (response.data) {
          TokenService.setSession(response.data);
        }
        return response.data;
      });
  }

  async logout() {
    try {
      TokenService.removeSession();
      const response = await authenticatedApi.post("/logout");
      
      return response.status == 200;
    } catch (error) {
      TokenService.removeSession();
    }
  }
}

export default new AuthService();
